// 변수 정의
$primary-color: #3498db; // 파란색
$secondary-color: #e74c3c; // 빨간색
$border-color: #ccc;
$log-box-border-color: #000;

// 기본 레이아웃
.clan-battle-container {
  display: flex;
  justify-content: space-around;
}

.clan-list {
  ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
  }
}


// 기본 레이아웃
.battle-arena {
  display: flex;
  justify-content: space-around;
}

.clan-group {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.clan-list ul {
  display: flex;
  flex-wrap: wrap;
}

.member {
  margin: 10px;
  width: 100%;
  position: relative;
  text-align: center;
  margin-bottom: 10px; 

  &:hover .member-tooltip {
    display: block;
  }

  &-icon {
    display: block;
    margin-bottom: 5px;
  }

  &-info {
    display: flex;
    align-items: center;
    width: 100%; 
    box-sizing: border-box; 


    .name {
      margin-right: 10px;

      &.attacker {
        color: $primary-color;
        font-weight: bold;
      }

      &.defender {
        color: $secondary-color;
        font-weight: bold;
      }
    }

    .attack-icon {
      margin: 0 5px; // 아이콘 좌우 간격 조정
    }
  }
}

.clan-list:nth-child(1) .name,
.clan-list .clan1-icon + .member-info .name { /* 첫 번째 문파 목록 스타일 */
  color: rgb(20, 20, 230); // 여기에 { } 추가 
}

.clan-list:nth-child(2) .name,
.clan-list .clan2-icon + .member-info .name { /* 두 번째 문파 목록 스타일 */
  color: rgb(236, 29, 29); // 여기에 { } 추가
}

/* 추가 스타일 */
.clan-list .dialogue-area {
  width: 100%;
  text-align: center;
}

.skill-log .log-entry {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  white-space: normal; /* 텍스트 줄 바꿈 허용 */
  flex-wrap: wrap; /* 여러 줄로 표시될 수 있도록 설정 */
  word-break: break-all; /* 단어 단위로 줄 바꿈 */
}

.skill-log .log-entry > span {
  max-width: 100%; /* 각 span 요소의 최대 너비를 제한 */
  overflow: hidden; /* 넘치는 부분 숨김 */
  text-overflow: ellipsis; /* 넘치는 부분 말줄임표 처리 */
}

// 멤버 정보 스타일
.name {
  &.attacker {
    color: $primary-color;
    font-weight: bold;
  }

  &.defender {
    color: $secondary-color;
    font-weight: bold;
  }
}

.member-battle {
  margin-top: 10px;
}

// 체력 바 스타일
.health-bar {
  width: 40px; // 너비 조절
  height: 10px;
  border: 1px solid black;
  display: inline-block;
  margin: 0 5px; // 좌우 마진 추가

  .health {
    height: 100%;
    background-color: rgb(79, 141, 79); // clan1 기본 색상
  }

  // clan2Members의 체력 바 색상 변경
  .clan2-icon + .member-info & .health {  // 두 번째 clan-list의 .health 요소 선택
    background-color: $secondary-color; // 빨간색 변수 사용
  }

  &.debuffed .health {
    background-color: orange;
  }
}

// 상태 표시 스타일
.status {
  font-size: smaller;
  color: gray;
}

// 대화 영역 스타일
.dialogue-area {
  width: 100%;
  text-align: center;
  margin-top: 20px;
}

.dialogue {
  font-size: 12px;
  width: 100%;
  text-align: center;
  padding: 10px;
  margin-bottom: 10px;
  position: relative;

  &-icon {
    position: absolute;
    top: 5px;
    left: 5px;
    font-size: 20px;
  }

  &.attacker-dialogue {
    background-color: $primary-color;
    color: white;
    border: 2px solid darken($primary-color, 10%);

    &.skill-used {
      background-color: #9b59b6;
      border: 2px solid darken(#9b59b6, 10%);
    }
  }

  &.defender-dialogue {
    background-color: $secondary-color;
    color: white;
    border: 2px solid darken($secondary-color, 10%);
  }
}

// 스킬 로그 스타일
.skill-log {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid $border-color;
  border-radius: 5px;

  h3 {
    margin-bottom: 10px;
    font-size: 18px;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      margin-bottom: 5px;
      display: flex;
      align-items: center;

      ul {
        margin-left: 20px;
      }
    }
  }

  .member-name-level {
    margin-right: 10px;
  }
}

.log-container {
  display: flex;
  flex-direction: column;
}

.log-box {
  border: 1px solid $log-box-border-color;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;

  p {
    margin: 5px 0;
  }
}

.dialogues {
  margin-top: 10px;
  font-style: italic;

  p {
    margin: 0;
  }
}

.heal-log {
  background-color: #c7ecee;
}

.clan-message {
  font-size: 18px;
  padding: 10px;
  margin-bottom: 10px;
  text-align: center;

  &.winner-message {
    background-color: #2ecc71; // 밝은 녹색 배경
    color: white;
    font-weight: bold;
  }

  &.loser-message {
    background-color: #e74c3c; // 빨간색 배경
    color: white;
    opacity: 0.8; // 약간 투명하게
  }
}

.battle-result {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid $border-color;
  border-radius: 5px;
  text-align: center; // 텍스트 가운데 정렬

  h2 {
    margin-bottom: 10px;
    font-size: 24px;
  }

  h3 {
    margin-top: 10px;
    font-size: 20px;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0 0 20px 0; // 아래 여백 추가

    li {
      margin-bottom: 5px;
    }
  }


}

.defense-bonus-applied {
  color: blue; 
}

.shimmer {
  background: none; /* 배경 없음 */
  width: 100%;
  position: relative;
  overflow: hidden;

  &::before { /* shimmer 효과를 위한 가상 요소 추가 */
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: inherit; /* 부모 요소의 배경 상속 */
    z-index: 1; /* 기존 내용 위에 겹쳐지도록 설정 */
  }

  > * { /* shimmer 요소의 모든 자식 요소에 적용 */
    position: relative; /* 자식 요소를 relative positioning으로 변경 */
    z-index: 2; /* 가상 요소보다 위에 위치하도록 설정 */
  }

  .skill-info {
    /* shimmer 효과가 적용되는 동안에도 skill-info 내용 보이도록 수정 */
    opacity: 1; /* 불투명하게 */
    animation: none; /* shimmer 애니메이션 제거 */
    background: none; /* shimmer 배경 제거 */
  }
}

.skill-info {

  .skill-name {
    font-weight: bold;
    margin-right: 5px; /* 오른쪽 여백 추가 */
    text-align: center; /* 텍스트 가운데 정렬 */
  }

  .dialogue {
    font-size: 10px;
    color: rgb(83, 83, 83);
    text-align: center; /* 텍스트 가운데 정렬 */
  }
}
